//@flow
import * as React from "react";
import AppBar from "../components/AppBar";
import SideBar from "../components/SideBar";
import MenuButton from "../components/MenuButton";
import Helmet from "react-helmet";
import withRoot from "../containers/withRoot";
import { AppStateProvider } from "../containers/AppState";
import withStyles from "@material-ui/core/styles/withStyles";
import { APPBAR_MIN_HEIGHT } from "../config";
import Hidden from "@material-ui/core/Hidden";

type Props = {
  children: () => React.Node,
  data: any,
  classes: MUICLasses
};

const classes = withStyles(theme => ({
  root: {
    display: "flex",
    position: "relative",
    width: "100%",
    zIndex: 1,
    overflow: "hidden",
    backgroundColor: theme.palette.background.paper
  },
  main: {
    flex: 1,
    margin: `${APPBAR_MIN_HEIGHT}px auto 0 auto`,
    padding: 16,
    display: "flex",
    justifyContent: "center"
  }
}));

class Layout extends React.Component<Props> {
  render() {
    const { classes, children, data } = this.props;

    return (
      <AppStateProvider>
        <div id="shell" className={classes.root}>
          <Helmet
            title={data.site.siteMetadata.title}
            meta={[
              { name: "description", content: "Sample" },
              { name: "keywords", content: "sample, something" }
            ]}
          />
          <AppBar>
            <Hidden smUp>
              <MenuButton />
            </Hidden>
          </AppBar>
          <SideBar>
            <p>dff</p>
          </SideBar>
          <main className={classes.main}>{children()}</main>
        </div>
      </AppStateProvider>
    );
  }
}

export default withRoot(classes(Layout));

/* eslint-disable-next-line no-undef */
export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`;



